import React from 'react';
import './Introduction.css';
import { NavLink } from 'react-router-dom';

const Introduction = () => {
  return (
    <section className="introduction">
      <div>
        <h1>GLANCAM</h1>
      </div>
      <div>
        <p>
          Modernizando e inovando empresas para o mercado, através da criação de
          sites, utilizando das melhores tecnologias disponíveis.
        </p>
      </div>
      <div>
        <NavLink to="orcamento" className="a" end>PEÇA SEU ORÇAMENTO</NavLink>
      </div>
    </section>
  );
};
export default Introduction;
