import React from 'react'
import { NavLink } from 'react-router-dom'
import './Header.css'
import logo from './imgs/logo-glancam-lar-amar.svg'


const Header = () => {
  return (
    <>
      <header className="header-hamburguer">
        	<div className="header-logo">
				<img src={logo} alt="<GLANCAM/>"/>
        	</div>
			<input class="menu-btn" type="checkbox" id="menu-btn" />
			<label class="menu-icon" for="menu-btn"><span class="navicon"></span></label>
			<ul class="menu">
				<li><a href="http://glancam.com.br/#sobre">Sobre</a></li>
				<li><a href="http://glancam.com.br/#servicos">Serviços</a></li>
				<li><a href="http://glancam.com.br/#portfolio">Portfólio</a></li>
				<li><a href="http://glancam.com.br/#contato">contato</a></li>
			</ul>
      </header>
      <header className="header">
        <div className="header-logo">
          <img src={logo} alt="<GLANCAM/>"/>
        </div>
        <div className="header-menu">
          <nav>
            <a href="/#sobre">SOBRE</a>
            <a href="/#servicos">SERVIÇOS</a>
            <a href="/#portfolio">PORTFÓLIO</a>
            <a href="/#contato">CONTATO</a>
          </nav>
        </div>
      </header>
    </>
  )
}
export default Header