import React from 'react';
import './Orcamento.css';
import emailjs from 'emailjs-com';



function sendEmail(e) {
  e.preventDefault();

  emailjs.sendForm('Glancam', 'Contato', e.target, 'user_7xn40FwPE9jlLphP1bAiM')
    .then((result) => {
        console.log(result.text);
        alert("Dados enviados com Sucesso")
    }, (error) => {
        console.log(error.text);
    });
    e.target.reset();
}

const Orcamento = () => {
  return (
    
    <section className="orcamento" value={(window.scrollTo(0, 0))}>
      
      <form
        
        className="orcamento-forms "
        onSubmit={sendEmail}
      >
      
        <label>&lt;PEÇA SEU ORÇAMENTO/&gt;</label>
        <label>Nome:</label>
        <input
          id="1"
          type="text"
          className="vermelho"
          placeholder="Seu nome"
          name="name"
          required
        />
        <label>Email:</label>
        <input
          id="2"
          type="email"
          className="amarelo"
          minLength="7"
          pattern="(\W|^)[\w.\-]{0,25}@[a-z0-9]+\.com(\W|$)"
          name="email"
          required
        />
        <label>Celular:</label>
        <input
          type="tel"
          className="vermelho"
          id="phone"
          name="phone"
          placeholder="11912345678"
          pattern="[[0-9]{7}[0-9]{4}"
          name="celular"
          required
        />
        <label>Mensagem:</label>
        <textarea className="amarelo" name="message" id="4" required />

        <button
         
          type="submit"
          className="vermelho botao-orcamento"
          value="Send"
        >
          ENVIAR
        </button>
      </form>
    </section>
  );
};

export default Orcamento;
